// FONTS

@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap");

// @import url("https://fonts.cdnfonts.com/css/open-sans-4");
// =====

* {
  margin: 0;
  padding: 0;

  box-sizing: border-box;

  font-family: "Open Sans", sans-serif;
}

@media (max-width: 1920px) {
  .numbers {
    height: 1044px;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;

    .numbers-container {
      height: 894px;
      width: 1720px; //to vw

      display: flex;
      flex-flow: column wrap;
      justify-content: space-between;

      .numbers-title {
        font-size: 48px; //to vw
        font-weight: 600;
        color: #2887d9;
      }

      .cards-container {
        height: 800px;
        width: 1720px; //to vw

        display: flex;
        flex-flow: row wrap;
        overflow: hidden;

        border: none;
        border-radius: 50px;

        .cards-about {
          height: 400px;
          width: 860px; //to vw

          background-repeat: no-repeat;
          display: flex;

          div {
            height: 186px;
            width: 500px;

            display: flex;
            flex-flow: column;
            align-items: start;
            justify-content: space-between;

            margin-top: 50px;

            h1 {
              font-family: "Open Sans", sans-serif;
              font-size: 150px;
              font-weight: 300;
            }

            p {
              font-size: 30px;
              font-weight: 600;
              color: #2887d9;
            }
          }
        }

        .cards-about:nth-child(odd) div {
          margin-left: 195px;
        }

        .cards-about:nth-child(even) div {
          margin-left: 70px;
        }

        #feedback {
          background-color: #f3dfea;
          background-image: url(./tif/heart.svg), url(./tif/syringe.svg);
          background-size: 193px 174px, 206px 206px;
          background-position: -41px 145px, 40px -82px;

          h1 {
            color: #edbbd6;
          }
        }

        #vaccinations {
          position: relative;
          background-color: #d0e1f1;
          background-image: url(./tif/pills.svg);
          background-size: 172px 154px;
          background-position: 548px -104px;

          h1 {
            color: #72b7f2;
          }
        }

        #vaccinations::after {
          content: "";
          position: absolute;
          top: 113px;
          left: 720px;

          width: 144px;
          height: 124px;

          background-image: url(./tif/heart.svg);
          background-size: 144px 124px;
          transform: scaleX(-1);
        }

        #researches {
          background-color: #cef2f0;
          background-image: url(./tif/drip.svg);
          background-size: 177px 177px;
          background-position: -102px 154px;

          h1 {
            color: #58d7d0;
          }
        }

        #seniority {
          background-color: #fce296;
          background-image: url(./tif/stethoscope.svg);
          background-size: 240px 345px;
          background-position: 720px 27px;

          h1 {
            color: #facf4f;
          }
        }
      }
    }
  }
}

